@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100..900&display=swap');

//! Typography

$font: 'Inter', sans-serif;

/* Heading */

%Heading1 {
  font-weight: 600;
  font-size: 48px;
}

%Heading2 {
  font-weight: 600;
  font-size: 40px;
}

%Heading3 {
  font-weight: 600;
  font-size: 32px;
}

%Heading4 {
  font-weight: 600;
  font-size: 24px;
}

%Heading5 {
  font-weight: 600;
  font-size: 20px;
}

%Heading5 {
  font-weight: 600;
  font-size: 18px;
}

/* Body */

/* SemiBold */
%BodyLarge-Semibold {
  font-weight: 600;
  font-size: 18px;
}

%bodyMedium-Semibold {
  font-weight: 600;
  font-size: 16px;
}

%BodySmall-Semibold {
  font-weight: 600;
  font-size: 14px;
}

%BodyXSmall-Semibold {
  font-weight: 600;
  font-size: 12px;
}

/* Medium */

%BodyLarge-Medium {
  font-weight: 500;
  font-size: 18px;
}

%bodyMedium-Medium {
  font-weight: 500;
  font-size: 16px;
}

%BodySmall-Medium {
  font-weight: 500;
  font-size: 14px;
}

%BodyXSmall-Medium {
  font-weight: 500;
  font-size: 12px;
}

/* Regular */

%BodyLarge-Regular {
  font-weight: 400;
  font-size: 18px;
}

%bodyMedium-Regular {
  font-weight: 400;
  font-size: 16px;
}

%BodySmall-Regular {
  font-weight: 400;
  font-size: 14px;
}

%BodyXSmall-Regular {
  font-weight: 400;
  font-size: 12px;
}

