@import '../.././_variables';

.nav {
  display: flex;
  align-items: center;
  align-items: center;
  width: 100%;
  padding: 0 100px;
  gap: 20px;
  height: 116px;
  position: sticky;
  top: 0;
  justify-content: space-between;
  z-index: 999;

  ul {
    display: flex;
    gap: 40px;

    li {
      list-style: none;
      color: $Primary0;
      transition: 300ms;
      cursor: pointer;
      @extend %bodyMedium-Medium;
      &:hover {
        color: $Primary300;
      }
    }
  }
}


  

.scroll {
  background-color: $Background;
  width: 100%;
  transition: 400ms;
  border-bottom: 1px solid $Primary300;

  button {
    border: 1px solid $Primary0;
    color: $Primary0;
  }
}
