@import '../.././_variables';

.card {
  width: 200px;
  height: 250px;
  border-radius: 24px;
  contain: content;

  .cardInfo {
    background-color: #ffffff10;
    backdrop-filter: blur(10px);
    position: absolute;
    display: flex;
    flex-direction: column;
    gap: 16px;
    width: 100%;
    padding: 10px;
    align-items: center;
    bottom: 0;

    .name {
      font-size: 12px;

      font-weight: 700;
    }

    .price {
      display: flex;
      justify-content: space-around;
      width: 100%;
      align-items: center;
      .texts {
        display: flex;
        flex-direction: column;
        gap: 6px;
        align-items: center;
        label {
          color: #646464;
          font-size: 10px;
          font-weight: 700;
        }
        div {
          display: flex;
          gap: 4px;
          img {
            height: 16px;
          }
          p {
            font-size: 10px;
            font-weight: 700;
          }
        }
      }
    }
  }
}
