@import '../.././_variables';

.footer {
  display: flex;
  flex-direction: column;
  gap: 48px;
  text-align: center;
  padding: 32px 100px;
  background-color: $Greyscale800;

  label {
    color: $Primary0;
    @extend %bodyMedium-Regular;
  }

  .content {
    display: flex;
    justify-content: space-between;
    text-align: left;

    .social {
      display: flex;
      flex-direction: column;
      gap: 50px;
      .texts {
        display: flex;
        flex-direction: column;
        gap: 22px;

        p {
          @extend %BodyLarge-Medium;
          color: $Primary0;
        }

        img {
          width: 52px;
        }
      }

      .buttons {
        display: flex;
        gap: 12px;
        div {
          text-align: center;
          background-color: #161616;
          border-radius: 50%;
          width: 28px;
          display: flex;
          justify-content: center;
          height: 28px;
          align-items: center;
          i {
            color: $Primary0;
            font-size: 14px;
          }
        }
      }
    }

    .links {
      display: flex;
      gap: 77px;

      b {
        color: $Primary0;
        font-size: 18px;
        font-weight: 700;
      }

      ul {
        margin-top: 16px;
        display: flex;
        flex-direction: column;
        gap: 16px;

        li {
          list-style: none;
          color: $Primary0;
          @extend %BodyLarge-Regular;
          cursor: pointer;
          transition: 300ms;
          &:hover {
            color: $Primary300;
          }
        }
      }
    }
  }
}
