@import '../.././_variables';

.Welcome {
  margin-top: 83px;
  padding: 0 100px;
  display: flex;
  flex-direction: column;
  flex: 1;

  img {
    user-select: none;
  }

  .content {
    display: flex;
    flex-direction: column;
    flex: 1;

    .label {
      border: 1px solid $Primary0;
      background-color: #ffffff05;
      padding: 8px;
      border-radius: 100px;
      color: $Primary0;
      width: 222px;
      cursor: pointer;
      text-align: center;
      transition: 300ms;
      &:hover {
        background-color: $Primary300;
        border: 1px solid $Primary300;
      }
    }

    .texts {
      display: flex;
      flex-direction: column;
      gap: 16px;
      margin-top: 24px;

      h1 {
        font-size: 80px;
        font-weight: 600;
        color: $Primary0;
        gap: 20px;
        label {
          color: $Primary300;
        }
      }

      p {
        @extend %BodyLarge-Medium;
        color: $Primary0;
        max-width: 700px;
      }
    }

    .buttons {
      display: flex;
      justify-content: flex-start;
      gap: 48px;
      margin-top: 48px;
    }
  }

  .cards {
    .card {
      width: 200px;
      height: 250px;
      border-radius: 24px;
      contain: content;

      .cardInfo {
        background-color: #ffffff10;
        backdrop-filter: blur(10px);
        position: absolute;
        display: flex;
        flex-direction: column;
        gap: 16px;
        width: 100%;
        padding: 10px;
        align-items: center;
        bottom: 0;

        .name {
          font-size: 12px;
          font-weight: 700;
        }

        .price {
          display: flex;
          justify-content: space-around;
          width: 100%;
          align-items: center;
          .texts {
            display: flex;
            flex-direction: column;
            gap: 6px;
            align-items: center;
            label {
              color: #646464;
              font-size: 10px;
              font-weight: 700;
            }
            div {
              display: flex;
              gap: 4px;
              img {
                height: 16px;
              }
              p {
                font-size: 10px;
                font-weight: 700;
              }
            }
          }
        }
      }
    }

    .card:nth-child(1) {
      position: absolute;
      top: 167px;
      right: 370px;
    }

    .card:nth-child(2) {
      position: absolute;
      top: 455px;
      right: 35px;
    }

    .card:nth-child(3) {
      position: absolute;
      top: 638px;
      right: 416px;
    }
  }
}

@media only screen and (max-width: 1400px) {
  .cards {
    display: none;
  }
}
