@import '../../../_variables';
.companies {
  margin-top: 226px;
  overflow: hidden;
  white-space: nowrap;
}

.slide {
  animation: 20s Slide infinite linear;
  display: inline-block;
  img {
    height: 45px;
    margin: 0 40px;
    cursor: pointer;
  }
}

.companies:hover .slide {
  animation-play-state: paused;
}

@keyframes Slide {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-100%);
  }
}
