@import '_variables';

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: $font;
  text-decoration: none;
}

html,
body,
#root {
  width: 100%;
}

label {
  cursor: text;
}

body {
  background-color: $Background;

  .blur1,
  .blur2 {
    background-color: $Primary300;
    width: 228px;
    height: 328px;
    border-radius: 50%;
    position: absolute;
    filter: blur(200px);
  }

  .blur1 {
    left: -150px;
    top: 147px;
  }

  .blur2 {
    right: 0;
    top: 560px;
  }

  .circle1,
  .circle2 {
    border: 1px solid #ffffff20;
    border-radius: 50%;
    position: absolute;
  }

  .circle1 {
    width: 580px;
    height: 580px;
    display: flex;
    justify-content: center;
    align-items: center;
    right: 92px;
    top: 253px;
  }

  .circle2 {
    width: 464px;
    height: 464px;
  }
}

@media only screen and (max-width: 1400px) {
  body {
    .circle1,
    .circle2 {
      display: none;
    }
  }
}
