@import '../.././_variables';

.offer {
  width: 100%;
  height: 1080px;
  padding: 0 100px;
  background: url(../../../public/images/background.jpg) no-repeat;
  background-size: cover;
  margin-top: 132px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  .texts {
    display: flex;
    flex-direction: column;
    gap: 16px;
    h3 {
      color: $Primary0;
      @extend %Heading2;
      label {
        color: $Primary300;
      }
    }

    p {
      color: $Primary0;
      @extend %BodyLarge-Semibold;
    }
  }

  img {
    width: 468px;
  }

  button {
    width: 151px;
    height: 51px;
    padding: 0;
  }
}
