@import './../../_variables';

.fill {
  background-color: $Primary300;
  user-select: none;
  padding: 16px 30px;
  border-radius: 12px;
  @extend %bodyMedium-Semibold;
  color: $Primary0;
  border: 1px solid $Primary300;
  cursor: pointer;
  transition: 300ms;
  text-wrap: nowrap;
  &:hover {
    background-color: transparent;
    border: 1px solid $Primary0;
  }
}

.default {
  background-color: transparent;
  user-select: none;
  padding: 16px 30px;
  border-radius: 12px;
  @extend %bodyMedium-Semibold;
  color: $Primary300;
  cursor: pointer;
  border: 1px solid $Primary300;
  transition: 300ms;
  text-wrap: nowrap;
  &:hover {
    background-color: $Primary300;
    color: $Primary0;
  }
}

.card {
  background-color: transparent;
  user-select: none;
  border: 1px solid #000;
  border-radius: 12px;
  width: 48px;
  height: 24px;
  font-size: 10px;
  font-weight: 700;
  transition: 300ms;
  cursor: pointer;
  &:hover {
    background-color: $Primary0;
    color: black;
  }
}
